import { getElement } from "../element/element";
import './footer.css';

export function getFooter() {
    const footer = getElement('footer', 'footer');
    const container = getElement('div', 'container', 'footer__container');

    const policy = getElement('div', 'footer__policy');
    policy.textContent = ' ';
    const phone = getElement('a', 'footer__phone');
    phone.textContent = '+7 777 777 77 77';

    container.append(policy, phone);
    footer.append(container);
    return footer;
}