import Navigo from "navigo"
import { getHeader } from "./components/header/header";
import { getPageContainer } from "./components/pageContainer/pageContainer";
import { getFooter } from "./components/footer/footer";


const $wrapper = document.getElementById('wrapper');

let root = null;
let useHash = true; 
let hash = '#!';
export let router = new Navigo(root, useHash, hash);

const header = getHeader();
const pageContainer = getPageContainer();
const footer = getFooter();

router
    .on({
      '/': async () => { 
        pageContainer.innerHTML = "";
        const moduleMain = await import("/src/js/pages/home/home.js");
        const mainPage = moduleMain.getHome();
        pageContainer.append(mainPage);
        header.setActiveLink("home");

    },
      '/admin': async () => {
        pageContainer.innerHTML = "";
        const moduleAdmin = await import("/src/js/pages/admin/admin.js");
        const adminPage = moduleAdmin.getAdminPage();
        pageContainer.append(adminPage);
    },
    '/portfolio': async () => {
      pageContainer.innerHTML = "";
      const modulePortfolio = await import("/src/js/pages/portfolio/portfolio.js");
      const portfolioPage = modulePortfolio.getPortfolio();
      pageContainer.append(portfolioPage);
      header.setActiveLink("portfolio");
  },
      '/portfolio/:title': async (event) => {
        console.log(event);
        pageContainer.innerHTML = ""
        const moduleProduct = await import("/src/js/pages/itemPage/itemPage.js")
        const productPage = moduleProduct.getItemPage(event)
        pageContainer.append(productPage)
    }
}).resolve();

